@import './variable.scss';
@import './mixin.scss';
.product-container{
  width: 850px;
  margin: 0 auto;
  @include tablet-portrait-max{
     width: 80vw;
  padding: 0 5em;
  }
  @include mobile-portrait-max{

  }
}
.product-wrapper{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 40px;
    @include tablet-portrait-max{
         grid-template-columns: 1fr 1fr;
    }
    @include mobile-portrait-max{
         grid-template-columns: 1fr;
    }
}
.single-product{
    box-shadow: -10px 11px 7px -3px rgba(0,0,0,0.28);
    -webkit-box-shadow: -10px 11px 7px -3px rgba(0,0,0,0.28);
    -moz-box-shadow: -10px 11px 7px -3px rgba(0,0,0,0.28);
    display: flex;
    justify-content:  flex-start;
    align-items: center;
    flex-direction: column;
    border-radius: 10px;
    color: #777777;
    position: relative;
}
.single-product-text{
    display: flex;
    justify-content:  flex-start;
    align-items: center;
    flex-direction: column;
    padding: 15px 10px;
}
.single-product-img{
    height: 400px;
    width: 100%;
    overflow: hidden;
    object-fit: cover;
    z-index: 0;
    &:hover{
            // transform: scale(1.1);
            // transition: all 1.2s ease-out;
            
    }
}
.cart-btn{
    width: 100%;
    padding: 10px 0;
    border: none;
    background-color: $cart-btn-color;
    border-radius: 0 0 10px 10px;
    color: $cart-font-color;
    font-weight: bolder;
    padding: 15px 0;
    font-size: 16px;
    cursor: pointer;
}
@media only screen and (max-width: 1024px) {
  .product-wrapper{
    grid-template-columns: 1fr 1fr;
}
}
@media only screen and (max-width: 640px) {
.product-wrapper{
    grid-template-columns: 1fr;
}
}
